'use strict';
var React = require('react');
var Unit = require('lib/unit');

var SearchContent = React.createClass({
    getInitialState: function() {
        return {
            data: []
        }
    },
    clear: function() {
        F7.confirm('','', function() {
            console.log(1);
        });
    },
    render: function () {
        var dataList = this.state.data.map(function(data, index) {
            return (
                <a href={'view/goods_list.html?id='+data.id} key={index}>{data.name}</a>
            );
        });
        return (
            <div className="search-content">
                <div className="search-box">
                    <div className="hd flex flex-between flex-center">
                        
                        <img className="trash" src="img/trash.png" onClick={this.clear} />
                    </div>
                    <div className="bd">
                        <a href="#"></a>
                        <a href="#">2</a>
                        <a href="#">3</a>
                        <a href="#"></a>
                        <a href="#">2</a>
                        <a href="#">3</a>
                    </div>

                </div>
                <div className="search-box">
                    <div className="hd">
                        
                    </div>
                    <div className="bd">
                        {dataList}
                    </div>

                </div>
            </div>
        );
    },
    componentDidMount: function() {
        var that = this;
        Unit.ajax({
            api: 'search_hot'
        }, function(data) {
            if (data.status == 1) {
                that.setState({
                    data: data.data
                });
            }
        });
    }
});

module.exports = SearchContent;


