'use strict';
var React = require('react');
var Unit = require('lib/unit');

var PasswordBackContent = React.createClass({
	render: function () {
        return (
            <div className="password-back-content">
                <div className="registerbox">
                    <div className="sbox">
                        <label className="flex">
                            <span className="textbox"></span>
                            <input  className="account flex-col" type="text" placeholder="6-20" ref="account"/>
                        </label>
                    </div>
                    <div className="sbox fix">
                        <div className="flex">
                            <span className="textbox phonetext"></span>
                            <span  className="phone flex-col phonebig">158****7149</span>
                            <span className="code" ></span>
                        </div>
                    </div>
                    <div className="sbox">
                        <label className="flex">
                            <span className="textbox"></span>
                            <input  className="password flex-col" type="text" placeholder=""  ref="codenum"/>
                        </label>
                    </div>
                </div>
                <div className="passwordbtnbox">
                    <a  className="login-btn" onClick={this.register} href="view/password_change.html"></a>
                </div>
             </div>
        );
    }
});
module.exports = PasswordBackContent;


