'use strict';
var React = require('react');
var Unit = require('lib/unit');

var RegisterContent = React.createClass({
    getInitialState: function() {
        return {
            switchState: 0
        }
    },
    agreeSwitch: function() {
        var state = this.state.switchState;
        this.setState({
            switchState: !state
        });
    },
    register: function() {
        var that = this;
        var account = this.refs.account.getDOMNode().value;
        var password = this.refs.password.getDOMNode().value;
        var sameword = this.refs.sameword.getDOMNode().value;
        var phone = this.refs.phone.getDOMNode().value;
        var codenum = this.refs.codenum.getDOMNode().value;
        if (account == '') {
            F7.alert('');
            return;
        }
        if (password == '') {
            F7.alert('');
            return;
        }
        if (sameword != password ) {
            F7.alert('');
            return;
        }
        if (phone != "" ) {
            F7.alert('');
            return;
        }
        if (codenum == '') {
            F7.alert('');
            return;
        }
        F7.showIndicator();
        Unit.ajax({
            api: 'search_hot'
        }, function(data) {
            F7.hideIndicator();
            if (data.status == 1) {
                currentView.router.back();
            }
        });
    },
    render: function () {
        var state = this.state.switchState;
        var agreeClass = 'agree-img ';
        var loginClass = 'login-btn '
        if (!state) {
            agreeClass += 'active';
        } else {
            loginClass += 'disabled'
        }
        return (
            <div className="regiser-content">
                <div className="registerbox">
                    <div className="sbox">
                        <label  className="flex">
                            <span className="textbox"></span>
                            <input  className="account flex-col" type="text" placeholder="6-20" ref="account" maxlength="20 " />
                        </label>
                    </div>
                    <div className="sbox">
                        <label className="flex">
                            <span className="textbox"></span>
                            <input  className="password flex-col" type="password" placeholder="6-20" ref="password"/>
                        </label>
                    </div>
                    <div className="sbox">
                        <label className="flex">
                            <span className="textbox"></span>
                            <input  className="sameword flex-col" type="password" placeholder="6-20"  ref="sameword"/>
                        </label>
                    </div>
                    <div className="sbox fix">
                        <label className="flex">
                            <span className="textbox"></span>
                            <input  className="phone flex-col" type="text" placeholder="" ref="phone"/>
                            <div className="codebox"><span className="code" ></span></div>
                        </label>
                    </div>
                    <div className="sbox">
                        <label className="flex">
                            <span className="textbox"></span>
                            <input  className="password flex-col" type="text" placeholder=""  ref="codenum"/>
                        </label>
                    </div>
                </div>
                <div className="agreebox fix">
                    <div className="fix agree-sbox">
                        <span id="agree"  className={agreeClass} onClick={this.agreeSwitch}></span>
                        <span className="agree-text"><a href="">*****</a>,</span>
                    </div>
                </div>
                <div className="register-btnbox">
                    <span  className={loginClass} onClick={this.register}></span>
                </div>
            </div>
        );
    }
});

module.exports = RegisterContent;


