'use strict';


/**
 * API
 * @param type:get/post
 * @param data:
 * @param [successCallback]:
 * @param [errorCallback]:
 *
 */
var Unit = {
    url: 'data/',
    ajax: function(options,successCallback, errorCallback) {
        var _defaParams = {
            // appKey: "21725276",
            // page: 1,
            // rows: 10,
            // timestamp: Math.round(new Date().getTime() / 1000)
            // callback: "JSONP"
        }
        var params = options.params || {};
        for (var k in _defaParams) {
            if (typeof params[k] === "undefined") {
                params[k] = _defaParams[k];
            }
        }
        var method = options.method || "get";
        var api = options.api || "";
        var url = options.url || this.url + api + '.json';
        $$.ajax({
            method: method,
            url: url,
            data: params,
            dataType: "json",
            success: function(data) {
                successCallback(data);
            },
            error: function() {
                if (errorCallback) {
                    errorCallback();
                } else {
                    // Unit.loading("", 2000);
                }
            },
            timeout: 8000
        });
    }
};

// 
Unit.clientWidth = document.body.clientWidth;
// 
Unit.clientHeight = document.body.clientHeight;


/**
 * 
 * msg[string]: 
 * duration[number]: 
 */
Unit.toast = function(msg, duration) {
    var toast = $$('<div class="modal toast">'+msg+'</div>');
    if ($$('.toast').length) {
        return;
    }
    $$('body').append(toast);

    $$(toast).show();
    $$(toast).css({
        marginTop: - Math.round($$(toast).outerHeight() / 2) + 'px'
    });
    $$(toast).css({
        marginLeft: - Math.round($$(toast).outerWidth() / 2 / 1.185) + 'px' //1.185 
    });
    $$(toast).addClass('modal-in').transitionEnd(function () {

    });
    $$(toast).on('click', function() {
        remove();
    });
    function remove() {
        $$(toast).removeClass('modal-in').addClass('modal-out').transitionEnd(function () {
            $$(this).remove();
        });
    }
    setTimeout(function() {
        remove();
    }, duration || 1500);
};



/**
 * 
 * @param  {[string]} type ['y','ym',,'m',,'w',]
 * @return {[type]}      [description]
 */
Unit.getDate = function(type) {
    var date = new Date();
    var y = date.getFullYear();
    var m = fixTime(date.getMonth()+1);
    var w = date.getDay();
    var d = fixTime(date.getDate());
    var res = y +"/"+ m +"/"+ d;
    if (type == 'y') {
        res = y;
    } else if (type == 'ym') {
        res = y + '-' + m;
    } else if (type == 'm') {
        res = m;
    } else if (type == 'w') {
        res = '' + ''.charAt(w);
    }
    return res;
};

/**
 * n
 * @param n
 * @returns {string}
 * @constructor
 */
Unit.GetDateTime = function(date, n) {
    var dd = new Date(date);
    dd.setDate(dd.getDate()+n);
    var y = dd.getFullYear();
    var m = fixTime(dd.getMonth()+1);
    var d = fixTime(dd.getDate());
    return y+"/"+m+"/"+d;
};

/**
 * 
 * @return {[type]} [description]
 */
Unit.getSearch = function () {
    var search = window.location.search;
    if (search) {
        var queryArr = search.split('?')[1].split('&');
        var q = {};
        for (var k in queryArr) {
            var qArr = queryArr[k].split('=');
            q[qArr[0]] = qArr[1];
        }
        return q;
    } else {
        return null;
    }
};

/**
 * 
 * @param data 
 * @param items 
 * @returns {Array}
 */
Unit.fmtDataArry = function(data, items) {
    var page = [];
    var fmtData = [];
    var dataLen = data.length;
    page.length = Math.ceil(dataLen / items);
    for (var i = 0; i < page.length; i++) {
        var child = [];
        fmtData[i] = [];
        var len = items;
        if (dataLen <= items) {
            len = dataLen;
        }
        if (i > 0) {
            len = items*(i) + dataLen % items;
        }
        for (var m = i*items; m < len; m++) {
            child.push(data[m]);
        }
        fmtData[i] = child;
    }
    return fmtData;
};

Unit.fixTime = function(t) {
    if (t < 0) {
        t = "00";
    } else if (t < 10) {
        t = "0" +t;
    }
    return t;
};


module.exports = Unit;


